.d-flex{
    display: flex;
}
.justify-content-between{
    justify-content: space-between;
}
.align-item-center{
    align-items: center;
}
.header{
    height: 108px;
    background-color: $primary;
    padding: 0 88px;
    .logo a{
        background: url(https://hexschool.github.io/webLayoutTraining1st/week3/logo.png) no-repeat;
        width: 80px;
        height: 48.57px;
        display: block;
        @include hide-text;
    }
    .nav-bar{
        li{
            padding-left: 64px;
            color:$white;
            font-size: $font-h6;
            line-height: 30px;
        }
        a{
            display: block;
            color:$white;
            text-decoration: none;
            &:hover{
                border-bottom: 1px solid rgb(255, 255, 255);
                padding: 7px 0;
              }
        }
    }
}
@include desktop{
    .header{
        padding: 0 39px;
    }
    .nav-bar{
        li{
            padding-left: 40px;
        }
        a{
            color:#000;
        }
    }
}
@include mobile{
    .nav-bar{
        li{
            padding-left: 40px;
        }
    }
}

.footer{
    background-color: $primary;
    height: 200px;
}